import React, { useState, useEffect, Component } from "react";
import Slider from "react-slick";
import ReactTextTransition, { presets } from "react-text-transition";
import introImg from "./img/Web_dev.png";
import aboutHtml from "./img/html.png";
import aboutCss from "./img/css.png";
import aboutJs from "./img/javascript.png";
import aboutPython from "./img/python.png";
import aboutJava from "./img/java.png";
import aboutC from "./img/c.png";
import aboutReact from "./img/react.png";
import aboutSql from "./img/sql.png";
import aboutGns3 from "./img/gns3.png";
import cardPeci from "./img/card_peci.png";
import blobData from "./img/blob.png";
import github from "./img/github.png";
import linkedin from "./img/linked.png";
import email from "./img/email.png";
import dataPdf from "./pdf/Data_Centric_Machine_Learning_ops_for_Autonomous_Driving_censored.pdf";
import cvPdf from "./pdf/cv-eng.pdf";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const FirstSec = () => {
    const descriptions = [
        "web developer",
        "software developer",
        "computer engineer",
        "telematics engineer",
    ];

    const [index, setIndex] = useState(0);

    useEffect(() => {
        const interval = setInterval(() => {
            setIndex((prevIndex) => {
                return (prevIndex + 1) % descriptions.length;
            });
        }, 2000);

        return () => clearInterval(interval);
    });

    return (
        <>
            <section className="intro">
                <div className="introName">
                    <h1>Hi, my name is Victor</h1>

                    <h3>
                        I'm a&nbsp;
                        <ReactTextTransition
                            springConfig={presets.gentle}
                            style={{ margin: "0 4px" }}
                            inline
                        >
                            <b style={{ color: "#9153F4" }}>
                                {descriptions[index]}
                            </b>
                        </ReactTextTransition>
                    </h3>
                </div>
                <div className="introImg">
                    <img src={introImg} alt="Dev Logo" />
                </div>
            </section>
        </>
    );
};

const SecondSec = () => {
    return (
        <>
            <section className="about">
                <span>
                    <h1>About</h1>&nbsp;&nbsp;&nbsp;&nbsp;
                    <h2>me</h2>
                </span>
                <div className="about-content">
                    <p className="about-text">
                        I've been doing web development since 2013, started as a
                        self taught kid, using PHP, HTML, JavaScript and CSS,
                        now moving forward by learning React (This website was
                        made with React). <br></br>
                        <br></br>
                        I'm not limited to web development though, I graduated
                        in computer and telematics engineering at the&nbsp;
                        <a href="https://www.ua.pt/">University of Aveiro</a>,
                        where I learned some solid foundations about software
                        development, modeling and implementation of relational
                        databases, software integrations with hardware, computer
                        architecture, architecture of internet networks and
                        cibersecurity. My knowledge of programing languages
                        ranges from the highest level ones(e.g. python), to the
                        lowest level ones (e.g. assembly for MIPS).
                        <br></br>
                        As a hobby, I've also worked with logo design.
                        <br></br>
                        <br></br>
                        Check my{" "}
                        <a href={cvPdf} target="_blank">
                            CV
                        </a>{" "}
                        for more info.
                    </p>
                    <div className="about-img">
                        <div className="about-row">
                            <img src={aboutHtml} alt="" />
                            <img src={aboutCss} alt="" />
                            <img src={aboutJs} alt="" />
                        </div>
                        <div className="about-row">
                            <img src={aboutPython} alt="" />
                            <img src={aboutJava} className="java" alt="" />
                            <img src={aboutC} alt="" />
                        </div>
                        <div className="about-row">
                            <img
                                src={aboutReact}
                                className="react-logo"
                                alt=""
                            />
                            <img src={aboutSql} className="sql" alt="" />
                            <img src={aboutGns3} alt="" />
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
};

const FirstWaveSeparator = () => {
    return (
        <>
            <section className="firstWaveSep">
                <span>
                    <h2>my</h2>&nbsp;&nbsp;&nbsp;&nbsp;
                    <h1>Projects</h1>
                </span>
            </section>
        </>
    );
};

const ThirdSec = () => {
    return (
        <>
            <section className="projects">
                <div className="proj-text">
                    <h2>
                        Data-Centric Machine Learning for Autonomous Driving
                    </h2>
                    <div className="proj-text-desc">
                        <p>
                            This was a research project in which I took part as
                            a team manager of 4 engineering students.
                            <br />
                            I participated in this project while attending the
                            last year of my degree (2021-2022).
                            <br />
                            The project was about using a new process for
                            creating synthetic data (point clouds and labels)
                            for improving the quality of object detection models
                            for autonomous driving. The outcome of such research
                            was positive.
                        </p>
                    </div>
                    <a href={dataPdf} target="_blank">
                        <button>Read Paper</button>
                    </a>
                </div>
                <img src={blobData} className="proj-image" />
            </section>
        </>
    );
};

const SecondWaveSeparator = () => {
    return (
        <>
            <section className="secondWaveSep">
                <span>
                    <h2>my</h2>&nbsp;&nbsp;&nbsp;&nbsp;
                    <h1>Platforms</h1>
                </span>
            </section>
        </>
    );
};

const FourthSec = () => {
    return (
        <>
            <section className="platforms">
                <div className="plat-icons">
                    <a
                        href="https://github.com/victor-tm-souza"
                        className="git"
                        target="_blank"
                    >
                        <img src={github} />
                    </a>
                    <a
                        href="https://www.linkedin.com/in/victor-tms/"
                        className="linked"
                        target="_blank"
                    >
                        <img src={linkedin} />
                    </a>
                    <a
                        href="mailto: victms99@hotmail.com"
                        className="email"
                        target="_blank"
                    >
                        <img src={email} />
                    </a>
                </div>
            </section>
        </>
    );
};

const Footer = () => {
    return (
        <>
            <section className="footer">
                <p>Made by Victor Souza</p>
            </section>
        </>
    );
};

const App = () => {
    return (
        <>
            <FirstSec />
            <SecondSec />
            <FirstWaveSeparator />
            <ThirdSec />
            <SecondWaveSeparator />
            <FourthSec />
            <Footer />
        </>
    );
};

export default App;
